import React, { FC } from "react";
import styled from "styled-components";

import { Icon } from "../Icon/Icon";
import { colors, spacingMap } from "../styleguide";
import Typography from "../Typography/Typography";

import { ModalHeadingProps } from "./Modal.types";
import { useModalContext } from "./ModalContext";

const HeadingIcon = styled(Icon)`
	margin-right: ${spacingMap.xs}
`;

const ModalHeading: FC<ModalHeadingProps> = ({
	children,
	component,
	color,
	dataQA,
	headingIcon,
}) => {
	const { variant } = useModalContext();
	const headerIconColor = variant === "destructive" ? colors.red : colors.black;

	return (
		<>
			{headingIcon && <HeadingIcon name={headingIcon} color={headerIconColor} aria-hidden="true" />}
			<Typography
				dataQA={`${dataQA}-modal-heading`}
				component={component || "h1"}
				variant="headline"
				color={color}
			>
				{children}
			</Typography>
		</>
	);
};

ModalHeading.displayName = "ModalHeading";
export default ModalHeading;
